<template>
    <div id="lgts_login" v-show="state">
      <div>
        <form id="login" v-show="login_state" action="/operation/login/" method="post">
          <input v-model="username" placeholder="用户名">
          <input type="password" v-model="password" placeholder="密码">
          <input v-model="yzm" placeholder="验证码">
          <yzm @imgCode="imgCode"/>
          <!-- <button @click="login">登录</button> -->
          <input type="submit" value="提交">
          <a @click="changeShow">注册</a>
        </form>
        <form id="sign" v-show="!login_state">
          <input v-model="username" placeholder="用户名">
          <input type="password" v-model="password" placeholder="密码">
          <input type="email" v-model="email" placeholder="邮箱">
          <yzm @imgCode="imgCode"/>
          <input v-model="yzm" placeholder="验证码">
          <button @click="login">注册</button>
          <button @click="changeShow">返回登陆</button>
        </form>
      </div>
    </div>
</template>

<script>
  import Vue from 'vue'
  import v_img_verify from './components/v-img-verify'
  import VueResource from 'vue-resource'
  Vue.use(VueResource)
    export default {
        name: "lgts_login",
      components:{
        yzm:v_img_verify
      },
      data:function () {
        return {
          state:true,
          login_state:true,
          username:'',
          password:'',
          email:'',
          yzm:'',
          t_yzm:''
        }
      },
      methods:{
         login(){
           if((this.username)===null||(this.password)===null||this.yzm!=this.t_yzm){
              return false;
           }
           else{
             let data={username:this.username,password:this.password};
             if(this.login_state) {
              this.sendPost('login',data)
           }
           else{
               if(this.email!=null){
                 data.email=this.email;
                 this.sendPost('register',data)
               }
               else{

               }
             }
           }
         },
        sendPost(addUrl,data){
           this.$http.post('/operation/'+addUrl+'',data).then((res)=>{
             res=1;
             if(res===1){
               this.$router.push('/main')
             }
           })
           /*this.$router.push('/main')*/
        },
        changeShow(){
           this.login_state=!this.login_state;
        },
        imgCode(code) {
          this.t_yzm=code;
        }
      },

    }
</script>

<style scoped>

</style>



