<template>
    <div id="lunbo" :width="clientX+'px'">
      {{count}}
      <div class="image-warp" :style="{width:clientX*6+'px'}">
        <div v-for="(item,index) in mockImgUrl" :key="index" v-show="index===count" class="image-item" ><a><img  v-bind:src="item.image" :width="clientX+'px'"></a></div>
      </div>
    </div>
</template>

<script>
  import Mock from 'mockjs'
  var template={
    'image_arr|6':[{'image':"@image(200*200)"}]
  }
  var mockData=Mock.mock(template)
  //console.log(mockData.image_arr);
    export default {
        name: "Lgts_lunbo",
      data:function () {
        return{
           count:0,
           mockImgUrl:mockData.image_arr,
           clientX:0
        }
      },
      created:function () {
         this.play()
      },
      methods:{
        autoPlay(){
          this.count++;
          if(this.count===6){
            this.count=0
          }
        },
        play () {
          setInterval(this.autoPlay, 2500)
        },
      },
      mounted:function () {
        this.clientX=document.body.clientWidth;
      }
    }
</script>

<style scoped>
  #lunbo{
    overflow: hidden;
  }
  .image-warp{
    position: relative;
    height: 300px;
  }
  .image-item{
    position:relative;
    display: inline-block;
    float:left;
  }
a{
  position: relative;
}
  img{
    height: 300px;
  }
</style>



