import Vue from 'vue'
import Router from 'vue-router'
import lgts_login from '@/lgts_login'
import Lgts_main from '@/Lgts_main.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'lgts_login',
      component: lgts_login
    },
    {
      path:'/main',
      name:'Lgts_main',
      component:Lgts_main
    }
  ]
})



