<template>
    <div id="nav">
      <div v-bind:class="{'nav_item1':nIndex===index,nav_item:a,}" v-for="(item,index) in nav_items" v-bind:key="index" v-on:mouseenter="changeColor(item,index)" v-on:mouseout="mouseOut">{{item.text}}</div>
    </div>
</template>

<script>
  let nav_items=[
    {text:1},{text:2},{text:3}
  ];
  var a=true,b=false;

    export default {
        name: "lgts_nav",
      data:function () {
        return {
        nav_items:nav_items,
          a:true,
          b:false,
          nIndex:-1
      }
    },
      methods:{
        changeColor(item,index) {
          //console.log(item);
          //e.style.backgroundColor='#000'
          this.nIndex=index
          /*this.a=false
          this.b=true*/
        },
        mouseOut(){
          this.nIndex=-1
        }
      },
      created:function () {

      },
      updated:function () {

      },
      mounted:function(){

      }
    }
</script>

<style scoped>
  #nav{
    position:absolute;
    top: 0;
  }
  .nav_item{

    display: inline-block;
    width:300px;
    background: aqua;
  }
  .nav_item1{
    background: white;
  }
</style>



